import React, { useEffect, useState } from 'react';
import './teh-inspection.css';
import { useParams } from 'react-router';
import { httpFetch } from '../../../../services/http-fetch';
import { baseUrl } from '../../../../services/base-url';
import { dateToLocalTime } from '../../../../utils/date';
import { Checkbox, FormControlLabel, Typography } from '@material-ui/core';
import { TechnicalInspection } from '../../../../types/technical-inspection';
import techInspectionReplacer from '../../../../utils/techInspectionRaplacer';
import techInspectionTypeReplacer from '../../../../utils/techInspectionTypeRaplacer';
import { useHistory } from 'react-router-dom';
import { FetchData } from '../../../../gateways/http-fetch/http-fetch-response';
import Card from '../../common/card/card';
import { Check } from '@material-ui/icons';
import NavigationHeader from '../../common/navigation-header/navigation-header';

type TehInspectionId = {
  id: string;
};

const TehInspection = () => {
  const { id }: TehInspectionId = useParams();
  const history = useHistory();
  const [tehInspection, setTehInspection] = useState<FetchData>({ attributes: {} });

  useEffect(() => {
    httpFetch.get(`${baseUrl}/tech_inspections/${id}`).then(async data => {
      if (data) {
        await setTehInspection(data.data);
      } else {
        history.push('/technicalinspectionslist');
      }
    });
  }, []);

  const { attributes } = tehInspection;
  const {
    date,
    vehicle,
    mileage,
    mechanic,
    technical_condition,
    note,
    type_of_inspection,
    result,
    photo,
    lightning,
    braking_system,
    steering_wheel_play,
    clutch,
    tire_pressure,
    wipers,
    sound_signals,
    locks,
    fire_extinguisher,
    tow_rope,
    jack,
    first_aid_kit,
    warning_triangle,
  }: TechnicalInspection = attributes ? attributes : {};

  return (
    <div className="technical-inspection">
      <NavigationHeader>
        Техосмотр <b>№{id}</b>
      </NavigationHeader>
      {attributes && Object.keys(attributes).length !== 0 && (
        <>
          <Card className="field__med" color="white" label="Время прохождения осмотра">
            {dateToLocalTime(date)}
          </Card>

          <Card className="field__med" color="white" label="Марка">
            {vehicle && vehicle.manufacturer}
          </Card>

          <Card className="field__med" color="white" label="Модель">
            {vehicle && vehicle.model}
          </Card>

          <Card className="field__med" color="white" label="Год">
            {vehicle && vehicle.year}
          </Card>

          <Card className="field__med" color="white" label="Цвет">
            {vehicle && vehicle.color}
          </Card>

          <Card className="field__med" color="white" label="Гос. номер">
            {vehicle && vehicle.car_license_plate}
          </Card>

          <Card className="field__med" color="white" label="Пробег">
            {mileage}
          </Card>

          <Card className="field__med" color="white" label="Техническое состояние">
            {technical_condition}
          </Card>

          <Card className="field__med" color="white" label="Примечание">
            {note}
          </Card>

          {mechanic && (
            <>
              <Typography>Механик</Typography>

              <Card className="field__med" color="white" label="Механик">{`${
                mechanic && mechanic.last_name
              } ${mechanic && mechanic.first_name}`}</Card>
            </>
          )}

          <Card className="field__med" color="white" label="Результат осмотра">
            {techInspectionReplacer(result)}
          </Card>

          <Card className="field__med" color="white" label="Тип осмотра">
            {type_of_inspection ||
              (type_of_inspection === 0 && techInspectionTypeReplacer(type_of_inspection))}
          </Card>

          <FormControlLabel
            control={<Checkbox checkedIcon={<Check />} checked={lightning} name="checkedB" />}
            label="Осветительные приборы исправны"
          />
          <FormControlLabel
            control={<Checkbox checkedIcon={<Check />} checked={braking_system} name="checkedB" />}
            label="Тормозная система исправна"
          />
          <FormControlLabel
            control={
              <Checkbox checkedIcon={<Check />} checked={steering_wheel_play} name="checkedB" />
            }
            label="Люфт рулевого колеса в норме"
          />
          <FormControlLabel
            control={<Checkbox checkedIcon={<Check />} checked={clutch} name="checkedB" />}
            label="Сцепление исправно"
          />
          <FormControlLabel
            control={<Checkbox checkedIcon={<Check />} checked={tire_pressure} name="checkedB" />}
            label="Давление в шинах в норме"
          />
          <FormControlLabel
            control={<Checkbox checkedIcon={<Check />} checked={wipers} name="checkedB" />}
            label="Стеклоочистители исправны"
          />
          <FormControlLabel
            control={<Checkbox checkedIcon={<Check />} checked={sound_signals} name="checkedB" />}
            label="Звуковые сигналы исправны"
          />
          <FormControlLabel
            control={<Checkbox checkedIcon={<Check />} checked={locks} name="checkedB" />}
            label="Замки на двери, багажнике, капоте исправны"
          />
          <FormControlLabel
            control={
              <Checkbox checkedIcon={<Check />} checked={fire_extinguisher} name="checkedB" />
            }
            label="Огнетушитель"
          />
          <FormControlLabel
            control={<Checkbox checkedIcon={<Check />} checked={braking_system} name="checkedB" />}
            label="Тормозная система исправна"
          />
          <FormControlLabel
            control={<Checkbox checkedIcon={<Check />} checked={tow_rope} name="checkedB" />}
            label="Буксировочный трос"
          />
          <FormControlLabel
            control={<Checkbox checkedIcon={<Check />} checked={jack} name="checkedB" />}
            label="Домкрат"
          />
          <FormControlLabel
            control={<Checkbox checkedIcon={<Check />} checked={first_aid_kit} name="checkedB" />}
            label="Аптечка"
          />
          <FormControlLabel
            control={
              <Checkbox checkedIcon={<Check />} checked={warning_triangle} name="checkedB" />
            }
            label="Знак аварийной остановки"
          />

          {photo && (
            <>
              <Typography className="tech-title" variant="h5">
                Фото транцпорного средцтва
              </Typography>
              {photo.map((item, i) => (
                <Card key={i} className="field__med tech_file" label="Подпись" color="white">
                  <img src={item} alt="car photo" />
                </Card>
              ))}
            </>
          )}
        </>
      )}
    </div>
  );
};
export default TehInspection;
